* {
  box-sizing: border-box;
}

body {
  font-family: Poppins, -apple-system, Roboto, "Open Sans", "Helvetica Neue", sans-serif;
  line-height: 1.5;
  padding: 20px;
}

h3 {
  margin: 40px 0 0 0;
}

h4 {
  font-weight: bold;
  margin-top: 40px;
}

p {
  margin: 15px 0;
}

#main-doc header {
  text-align: left;
  font-size: 1.5rem;
  font-weight: bold;
  margin: 40px 0 20px 0;
}

header {
  font-size: 1.5rem;
  margin: 10px;
  text-align: center;
  font-weight: thin;
}

ol {
  list-style: decimal;
  padding-left: 20px;
}

.codebox {
  font-size: 1rem;
  background: #1d252c;
  color: #9cdcfe;
  display: block;
  text-align: left;
  position: relative;
  word-break: n;
  line-height: 1.5;
  padding: 20px;
  margin: 20px;
  white-space: nowrap;
  overflow-x: auto;
}

.codehighlight {
  font-size: 1rem;
  background: #d4dee4;
  display: inline-block;
}

.bluecode {
  color: #5391e7;
}

.greencode {
  color: #3d8f56;
}

.pinkcode {
  color: #e939a3;
}

.note {
  background: #e1f5fe;
  color: #01579b;
  display: block;
  text-align: left;
  position: relative;
  word-break: normal;
  word-wrap: normal;
  line-height: 2;
  padding: 20px 40px;
  margin: 20px 0;
  font-size: 1rem;
}

.tabspacing {
  width: 20px;
  white-space: pre;
}

#navbar {
  position: fixed;
  min-width: 290px;
  top: 0px;
  left: 0px;
  width: 300px;
  height: 100%;
  border-right: solid;
  border-color: rgba(91, 100, 100, 0.4);
  border-width: 0.1rem;
}

#main-doc {
  position: absolute;
  margin-left: 310px;
  padding: 20px;
  margin-bottom: 110px;
}

#navbar ul {
  height: 70%;
  padding: 0;
  overflow-y: auto;
  overflow-x: hidden;
}

#navbar ul li {
  list-style: none;
  margin-left: 0.5rem;
  margin-top: 5px;
  width: 95%;
  border-radius: 5px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
}

#navbar ul li:hover {
  background: #d9dce0;
}

#navbar ul li a {
  font-size: 1.2rem;
  padding: 10px 30px;
  text-decoration: none;
  color: #606770;
}

footer {
  text-align: center;
  font-size: 0.8rem;
  padding-top: 50px;
}

@media only screen and (max-width: 815px) {
  /* For mobile phones: */
  #navbar ul {
    max-height: 210px;
  }

  #navbar ul li {
    margin-top: 10px;
    margin-right: auto;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
  }

  #navbar {
    position: absolute;
    top: 0;
    padding: 0;
    margin: 0;
    width: 100%;
  }

  #main-doc {
    position: relative;
    margin-left: 0px;
    margin-top: 210px;
  }
}
@media only screen and (max-width: 400px) {
  #main-doc {
    margin-left: -10px;
    margin-bottom: 10px;
  }
}